.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  min-height: 270vh;
  margin: 0;
  padding: 0;
  font-family: "Public Sans", sans-serif;
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 1.5;
  color: hsl(233deg, 8%, 62%);
  overflow-x: hidden;
}
@media (min-width: 64em) {
  body {
    font-size: 1.125rem;
  }
}
body.noscroll {
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0px;
  font-weight: 300;
  line-height: 1.15;
  color: hsl(233deg, 26%, 24%);
}

h1 {
  font-size: 2.31rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3.25rem;
    margin-top: 0px;
  }
}

h2 {
  font-size: 1.875rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  h2 {
    font-size: 2.2rem;
    margin-bottom: 2.25rem;
  }
}

p {
  font-size: 1rem;
  margin-bottom: 2.125rem;
}
@media (min-width: 64em) {
  p {
    font-size: 1.125rem;
  }
}

a, a:visited, a:hover {
  text-decoration: none;
  font-size: 0.875rem;
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
}
.container--pall {
  padding-top: 4.5rem;
  padding-right: 1.5rem;
  padding-bottom: 3rem;
  padding-left: 1.5rem;
}
@media (min-width: 64em) {
  .container--pall {
    padding-top: 7rem;
    padding-bottom: 4rem;
  }
}
.container--py {
  padding-top: 4.5rem;
  padding-bottom: 3rem;
}
@media (min-width: 64em) {
  .container--py {
    padding-top: 7rem;
    padding-bottom: 4rem;
  }
}
.container--px {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
}
.container--pt {
  padding-top: 4.5rem;
}
@media (min-width: 64em) {
  .container--pt {
    padding-top: 7rem;
  }
}
.container--pr {
  padding-right: 1.5rem;
}
.container--pb {
  padding-bottom: 3rem;
}
@media (min-width: 64em) {
  .container--pb {
    padding-bottom: 4rem;
  }
}
.container--pl {
  padding-left: 1.5rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

button, .button {
  padding: 1rem 2.375rem;
  border-radius: 3.75rem;
  cursor: pointer;
  background: linear-gradient(to right, hsl(136deg, 65%, 51%), hsl(192deg, 70%, 51%));
  color: #fff;
  border: 0;
  font-family: "Public Sans", sans-serif;
  font-size: 0.85rem;
  font-weight: 550;
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}

button:hover, .button:hover {
  opacity: 0.7;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: None;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0rem;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(hsl(233deg, 26%, 24%), transparent);
}
.header nav {
  position: relative;
  background-color: #fff;
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.75rem;
  height: 1.375rem;
}
.header__toggle > span {
  display: block;
  width: 24px;
  height: 2px;
  background-color: hsl(233deg, 26%, 24%);
  transition: all 300ms ease-in-out;
  transform-origin: 2px 1px;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 5px;
}
.header__menu {
  background: hsl(0deg, 0%, 100%);
  padding: 1.625rem;
  border-radius: 3px;
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  margin-top: 1.5rem;
  border-radius: 0.3rem;
  z-index: 1;
}
@media (min-width: 64em) {
  .header__menu {
    visibility: hidden;
  }
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: hsl(233deg, 26%, 24%);
  text-align: center;
  font-size: 1rem;
}
.header__link a {
  position: relative;
  font-size: 0.875rem;
  color: hsl(233deg, 8%, 62%);
  transition: color 200ms ease-in-out;
}
.header__link a:not(:last-child) {
  margin-right: 32px;
}
.header__link a::before {
  content: "";
  position: absolute;
  width: 100%;
  display: block;
  height: 5px;
  left: 0;
  right: 0;
  top: 44px;
  background: linear-gradient(to right, hsl(136deg, 65%, 51%), hsl(192deg, 70%, 51%));
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}
.header__link a:hover {
  color: hsl(233deg, 26%, 24%);
}
.header__link a:hover::before {
  opacity: 1;
}

.hero {
  background-color: hsl(0deg, 0%, 98%);
}
@media (min-width: 64em) {
  .hero .container {
    min-height: 41rem;
    display: flex;
    align-items: center;
  }
}
.hero__image {
  position: relative;
  background-image: url("/images/bg-intro-mobile.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 60.2%;
  min-height: 16.188rem;
}
@media (min-width: 40em) {
  .hero__image {
    min-height: 25rem;
  }
}
@media (min-width: 64em) {
  .hero__image {
    flex: 3;
    order: 2;
    height: 41rem;
    background-image: none;
  }
}
@media (min-width: 64em) {
  .hero__image::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 201%;
    background-image: url("/images/bg-intro-desktop.svg");
    background-repeat: no-repeat;
    background-size: 93%;
    background-position: 0 77%;
  }
}
.hero__image::after {
  content: "";
  position: absolute;
  height: 106%;
  width: 100%;
  background-image: url("/images/image-mockups.png");
  background-size: 93%;
  background-repeat: no-repeat;
  background-position: center 95%;
}
@media (min-width: 40em) {
  .hero__image::after {
    background-size: 58%;
    height: 108%;
  }
}
@media (min-width: 64em) {
  .hero__image::after {
    height: 119%;
    width: 231%;
    background-size: 50%;
    background-position: 22% 91%;
  }
}
.hero__text {
  text-align: center;
}
@media (min-width: 64em) {
  .hero__text {
    flex: 2;
    order: 1;
    text-align: left;
  }
}

.feature {
  background-color: hsl(220deg, 16%, 96%);
  text-align: center;
}
@media (min-width: 40em) {
  .feature {
    text-align: left;
  }
}
.feature__start {
  margin-top: 1.375rem;
}
@media (min-width: 40em) {
  .feature__start {
    width: 60%;
  }
}
@media (min-width: 64em) {
  .feature__start {
    width: 65%;
    margin-top: 0;
  }
}
@media (min-width: 40em) {
  .feature__grid {
    display: flex;
    flex-wrap: wrap;
  }
}
.feature__item {
  padding: 0.9375rem;
}
@media (min-width: 40em) {
  .feature__item {
    flex: 0 0 50%;
  }
}
@media (min-width: 64em) {
  .feature__item {
    flex: 1;
  }
}
.feature__icon {
  margin-bottom: 1.75rem;
}
@media (min-width: 64em) {
  .feature__icon {
    margin-bottom: 2.875rem;
  }
}
.feature__title {
  font-weight: 300;
  line-height: 1.15;
  color: hsl(233deg, 26%, 24%);
  font-size: 1.25rem;
  margin-bottom: 1rem;
}
@media (min-width: 64em) {
  .feature__title {
    font-size: 1.5625rem;
    margin-bottom: 1.5rem;
  }
}
.feature__description {
  margin-bottom: 2.1875rem;
}

.articles {
  background-color: hsl(0deg, 0%, 98%);
}
.articles a {
  color: hsl(233deg, 8%, 62%);
}

.article__content {
  background-color: hsl(0deg, 0%, 98%);
}
.article__grid {
  display: grid;
  gap: 1.2rem;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  padding-top: 2.125rem;
}
@media (min-width: 40em) {
  .article__grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  .article__grid {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
  }
}
.article__item {
  margin-bottom: 2.5625rem;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0px 3px 15px rgba(231, 215, 215, 0.4);
  transition: 200ms ease-in-out;
  opacity: 0.8;
}
.article__item:hover {
  opacity: 2;
}
.article__text {
  background-color: hsl(0deg, 0%, 100%);
  padding: 1.75rem;
}
.article__image {
  height: 12.5rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.article__author {
  font-size: 0.825rem;
  margin-bottom: 0.875rem;
}
.article__title {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: black;
}
.article__description {
  font-size: 1.025rem;
  margin-bottom: 1rem;
}

.footer {
  color: hsl(0deg, 0%, 100%);
  padding-top: 4rem;
  text-align: center;
}
@media (min-width: 64em) {
  .footer {
    display: grid;
    grid-template: 50px 40px 40px 30px/20% 30% 1% 16% 10%;
    gap: 1rem 6.7rem;
    grid-template-areas: "logo menu p2 button p1" "social menu copy copy p1" "social menu copy copy p1" "bt bt bt bt bt";
  }
}
.footer a {
  color: hsl(0deg, 0%, 100%);
}
.footer__logo {
  display: inline-block;
  margin-bottom: 2rem;
}
@media (min-width: 64em) {
  .footer__logo {
    grid-area: logo;
  }
}
.footer__social {
  display: block;
  margin-bottom: 2rem;
}
.footer__social a:not(:last-child) {
  margin-right: 1rem;
}
@media (min-width: 64em) {
  .footer__social {
    grid-area: social;
  }
}
.footer__menu {
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  margin-bottom: 2rem;
}
.footer__menu a:not(:last-child) {
  margin-bottom: 1rem;
}
@media (min-width: 64em) {
  .footer__menu a:not(:last-child) {
    margin-bottom: 0px;
  }
}
@media (min-width: 64em) {
  .footer__menu {
    grid-area: menu;
    grid-template: 20px 20px 20px 50px/2fr 4fr;
    gap: 1rem;
    text-align: left;
    margin-bottom: 0px;
  }
}
.footer__button {
  margin-bottom: 5.7rem;
}
@media (min-width: 64em) {
  .footer__button {
    grid-area: button;
    margin-bottom: 0px;
  }
}
.footer__copy {
  font-size: 1rem;
  color: hsl(233deg, 8%, 62%);
}
@media (min-width: 64em) {
  .footer__copy {
    grid-area: copy;
    text-align: right;
  }
}
@media (min-width: 64em) {
  .footer__bigThank {
    grid-area: bt;
  }
}
.footer__whiteSpace {
  opacity: 0;
}
@media (min-width: 64em) {
  .footer__whiteSpace {
    grid-area: p1;
  }
}
.footer__blackSpace {
  opacity: 0;
}
@media (min-width: 64em) {
  .footer__blackSpace {
    grid-area: p2;
  }
}

.bg {
  background-color: hsl(233deg, 26%, 24%);
}